.profile {
    width: 90%;
    margin: auto;
    color: rgba(255, 255, 255, 0.8) !important;
    font-size: 17px;
    padding-bottom: 5px;
}

.profile .payments {
    overflow: auto;
}

.profile .payments h3 {
    text-align: center;
}

.payment-block {
    position: relative;
    padding-left: 10px;
    margin-bottom: 10px;
}

.payment-block::before {
    content: "";
    position: absolute; 
    top: 0;
    left: 0; 
    bottom: 0; 
    width: 3px;
    border-radius: 2px;
    background: rgba(255, 255, 255, 0.8) !important;
}