.study {
    width: 90%;
    margin: auto;
    color: rgba(255, 255, 255, 0.8) !important;
    font-size: 17px;
    padding-bottom: 5px;
}

.study p, h3 {
    margin-bottom: 10px;
}

.study ul {
    padding-left: 15px;
}

.lesson {
    position: relative;
    padding-left: 10px;
    margin-bottom: 10px;
}

.lesson::before {
    content: "";
    position: absolute; 
    top: 0;
    left: 0; 
    bottom: 0; 
    width: 3px;
    border-radius: 2px;
    background: rgba(255, 255, 255, 0.8) !important;
}

.list {
    margin: 0px 0px 10px 10px
}

.study img {
    width: 100%;
    margin-bottom: 10px;
}

.study button {
    height: 50px;
    width: 100%;
    background: rgba(255, 255, 255, 0.03);
    border-radius: 5px;
    font-size: 16px;
    border: none;
    padding-left: 20px;
    padding-right: 20px;
    color: rgba(255, 255, 255, 0.8) !important;
    outline: none;
    position: relative;
    margin-bottom: 15px;
}

.study button:hover {
    background: rgba(255, 255, 255, 0.1);
}