.private-channe {
    width: 90%;
    margin: 0px auto 5px auto;
    color: rgba(255, 255, 255, 0.8) !important;
    font-size: 17px;
}

.private-channe p, h3 {
    margin-bottom: 10px;
}

.private-channe ul {
    padding-left: 15px;
    margin-bottom: 10px;
}

.private-channe  button {
    height: 50px;
    width: 100%;
    background: rgba(255, 255, 255, 0.03);
    border-radius: 5px;
    font-size: 16px;
    border: none;
    padding-left: 20px;
    padding-right: 20px;
    color: rgba(255, 255, 255, 0.8) !important;
    outline: none;
    position: relative;
    margin-bottom: 15px;
}

.private-channe  button:hover {
    background: rgba(255, 255, 255, 0.1);
}
