.payment {
    width: 90%;
    margin: 0px auto 15px auto;
    color: rgba(255, 255, 255, 0.8) !important;
    font-size: 17px;
}

.payment p {
    margin-bottom: 10px;
}

.payment img {
    display: block;
    width: 90%;
    margin: 0px auto;
}

.payment .wallet {
    width: 90%;
    background: rgba(255, 255, 255, 0.03);
    border-radius: 5px;
    margin: 10px auto 15px auto;
    text-align: center;
    padding: 15px;
}

.payment .wallet p {
    height: fit-content;
    font-size: 15px;
    margin: 0;
    color: rgba(255, 255, 255, 0.8) !important;
}


@media(max-width: 360px) {
    .payment .wallet p {
        font-size: 13px;
    }
}