.one {
    background-color: red;
}

.close {
    width: fit-content;
    color: rgba(255, 255, 255, 0.8) !important;
    position: absolute;
    top: 4px;
    right: 7px;
    font-weight: 800;
}

.message {
    color: rgba(255, 255, 255, 0.8) !important;
    text-align: center;
    font-size: 18px;
}