.name, .email, .phone {
    width: 80%;
    margin: 0px auto 15px auto;
}

.name input, .email input, .phone input {
    height: 50px;
    width: 100%;
    background: rgba(255, 255, 255, 0.03);
    border-radius: 5px;
    font-size: 16px;
    border: none;
    padding-left: 20px;
    padding-right: 20px;
    color: rgba(255, 255, 255, 0.8) !important;
    outline: none;
    position: relative;
    right: 20px;
}

input:focus {
    background: rgba(255, 255, 255, 0.1);
}
